import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/service/user.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  constructor(private userService: UserService,
    private router: Router) { }

  password: string = '';

  selectedFile: File = null;

  user: any = {
    name: '',
    nick: '',
    photo: ''
  }

  photo: any = {
    value: '',
    extension: ''
  }

  userPhoto: string = '';

  ngOnInit(): void {
    if (localStorage.length == 0) {
      this.router.navigate(['/'])
    }
    this.user = JSON.parse(localStorage.getItem('userInfo'));
    this.userPhoto = `https://semi1p2photo.s3.amazonaws.com/${this.user.photo}`;
    this.user.photo = this.userPhoto;
  }

  updateValues() {
    this.user.name = this.user.name.trim();
    this.user.nick = this.user.nick.trim();
    if (this.user.name == '' || this.user.nick == '') return;
    this.userService.updateUser({
      name: this.user.name,
      nick: this.user.nick,
      id: this.user.id,
      password: this.password
    }).subscribe(
      (res: any) => {
        if (res.status == 'error') {
          alert(res.message)
        } else {
          localStorage.setItem('userInfo', JSON.stringify(res))
          this.router.navigate(['/'])
        }
      }, (err) => console.error(err)
    )
  }

  updatePhoto() {
    if (this.selectedFile == null) return;
    var value = {
      id: this.user.id,
      password: this.password,
      photo: this.photo.value,
      extension: this.photo.extension
    }
    this.userService.updatePhoto(value).subscribe(
      (res: any) => {
        if (res.status == 'error') {
          alert(res.message)
        } else {
          localStorage.setItem('userInfo', JSON.stringify(res))
          this.router.navigate(['/'])
        }
      }, (err) => console.error(err)
    );
  }

  fileChange(event) {
    this.selectedFile = event.target.files[0];
    if (this.selectedFile == null) {
      this.user.photo = this.userPhoto;
      return;
    }
    this.getBase64(this.selectedFile);
  }

  getBase64(file: File) {
    let me = this;
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      me.photo.value = (reader.result as string).split(',').pop();
      me.photo.extension = file.name.split('.').pop();
      me.user.photo = `data:image/${me.photo.extension};base64,${me.photo.value}`
    };
    reader.onerror = function (error) {
      console.error(error);
    };
  }

}
