import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  constructor(private userService: UserService,
    private router: Router) { }

  user: any = {
    name: '',
    nick: '',
    password: '',
    password2: '',
  }

  selectedFile: File = null;

  photo: any = {
    value: '',
    extension: ''
  };

  imageLink: string = 'https://image.flaticon.com/icons/png/512/64/64572.png';

  ngOnInit(): void {
    if (localStorage.length > 0) {
      this.router.navigate(['/'])
    }
  }

  fileChange(event) {
    this.selectedFile = event.target.files[0];
    if (this.selectedFile == null) {
      this.imageLink = 'https://image.flaticon.com/icons/png/512/64/64572.png';
      return;
    }
    this.getBase64(this.selectedFile);
  }

  getBase64(file: File) {
    let me = this;
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      me.photo.value = (reader.result as string).split(',').pop();
      me.photo.extension = file.name.split('.').pop();
      me.imageLink = `data:image/${me.photo.extension};base64,${me.photo.value}`
    };
    reader.onerror = function (error) {
      console.error(error);
    };
  }

  createUser() {
    this.user.name = this.user.name.trim();
    this.user.nick = this.user.nick.trim();
    if (this.user.name == "" || this.user.nick == "") return;
    if (this.user.password != this.user.password2) return;
    if (this.selectedFile == null) return;
    var newUser: any = {
      name: this.user.name,
      nick: this.user.nick,
      password: this.user.password,
      photo: this.photo.value,
      extension: this.photo.extension
    }
    this.userService.createNewUser(newUser).subscribe(
      (res: any) => {
        if (res.status == 'error') {
          alert(res.message);
        } else {
          localStorage.setItem('userInfo', JSON.stringify(res))
          location.replace('/');
        }
      },
      (err) => console.error(err)
    )
  }

}
