import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import API_URI from './api-uri'

@Injectable({
  providedIn: 'root'
})
export class UserService {

  apiUri: string = API_URI;

  constructor(private http: HttpClient) { }

  createNewUser(value: any) {
    return this.http.post(`${this.apiUri}/user`, value);
  }

  loginUser(value: any) {
    return this.http.put(`${this.apiUri}/login`, value);
  }

  updateUser(value: any) {
    return this.http.put(`${this.apiUri}/updateUsers`, value);
  }
  
  makePost(value: any) {
    return this.http.post(`${this.apiUri}/post`, value);
  }

  getPost() {
    return this.http.get(`${this.apiUri}/post`);
  }

  updatePhoto(value: any) {
    return this.http.put(`${this.apiUri}/updateFoto`, value);
  }

  getUser() {
    return this.http.get(`${this.apiUri}/user`);
  }

  getChat(value: any) {
    return this.http.put(`${this.apiUri}/mensaje`, value);
  }

  postMessage(value: any) {
    return this.http.post(`${this.apiUri}/mensaje`, value);
  }

  translateValue(value: string) {
    return this.http.post(`${this.apiUri}/traducir`, {
      'text': value
    });
  }
}
