import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {

  constructor(private userService: UserService,
    private router: Router) { }

  userArray: any = [];
  currentUser: any = {
    id: ''
  };
  selectedUser: any = {
    _id: ''
  };

  allMessages: any = [];

  newMessage: string = '';

  ngOnInit(): void {
    if (localStorage.length == 0) {
      this.router.navigate(['/']);
      return;
    }
    this.currentUser = JSON.parse(localStorage.getItem('userInfo'));
    this.userService.getUser().subscribe(
      (res: any) => this.userArray = res.message,
      err => console.error(err)
    );
  }

  selectUser(user: any) {
    this.selectedUser = user;
    this.userService.getChat({
      user1: this.currentUser.id,
      user2: this.selectedUser._id
    }).subscribe(
      (res: any) => this.allMessages = res.message,
      err => console.error(err)
    );
  }

  getDateValue(value: string) {
    var dateValue = new Date(value);
    return `${dateValue.toLocaleDateString('es-GT')} ${dateValue.toLocaleTimeString('es-GT')}`;

  }

  postNewMessage() {
    this.newMessage = this.newMessage.trim();
    if (this.newMessage == '') return;
    this.userService.postMessage({
      user1: this.currentUser.id,
      user2: this.selectedUser._id,
      mensaje: this.newMessage
    }).subscribe(
      () => {
        this.updateMessages();
        this.newMessage = '';
      },
      (err) => console.error(err)
    );
  }

  updateMessages() {
    this.userService.getChat({
      user1: this.currentUser.id,
      user2: this.selectedUser._id
    }).subscribe(
      (res: any) => this.allMessages = res.message,
      err => console.error(err)
    );
  }

}
