import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/service/user.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private router: Router,
    private userService: UserService) { }

  user: any = {
    photo: ''
  };

  postArray: any = [];

  newPost: any = {
    text: '',
    photo: '',
    extension: '',
    link: 'https://pngimage.net/wp-content/uploads/2018/06/no-image-available-icon-png-7.png'
  };

  selectedFile: File = null;

  ngOnInit(): void {
    if (localStorage.length == 0) {
      this.router.navigate(['/login'])
      return;
    }
    this.user = JSON.parse(localStorage.getItem('userInfo'));
    if (this.user != null) {
      this.user.photo = `https://semi1p2photo.s3.amazonaws.com/${this.user.photo}`;
    } else {
      this.user = {
        photo: ''
      };
    }
    this.getPost();
  }

  fileChange(event) {
    console.log(event);
    this.selectedFile = event.target.files[0];
    if (this.selectedFile == null) {
      this.newPost.link = 'https://pngimage.net/wp-content/uploads/2018/06/no-image-available-icon-png-7.png';
      return;
    }
    this.getBase64(this.selectedFile);
  }

  getBase64(file: File) {
    let me = this;
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      me.newPost.photo = (reader.result as string).split(',').pop();
      me.newPost.extension = file.name.split('.').pop();
      me.newPost.link = `data:image/${me.newPost.extension};base64,${me.newPost.photo}`
    };
    reader.onerror = function (error) {
      console.error(error);
    };
  }


  makeNewPost() {
    this.newPost.text = this.newPost.text.trim();
    if (this.newPost.text == '') return;
    var value;
    if (this.selectedFile == null) {
      value = {
        creator: this.user.id,
        publicacion: this.newPost.text,
      } 
    } else {
      value = {
        creator: this.user.id,
        publicacion: this.newPost.text,
        Foto: this.newPost.photo,
        extension: this.newPost.extension
      } 
    }
    this.userService.makePost(value).subscribe(
      (res) => 
      {
        this.newPost = {
          text: '',
          photo: '',
          extension: '',
          link: 'https://pngimage.net/wp-content/uploads/2018/06/no-image-available-icon-png-7.png'
        };
        this.selectedFile = null;
        this.getPost();
      },
      (err) => console.error(err)
    );
  }

  getPost() {
    this.userService.getPost().subscribe(
      (res: any) => {
        this.postArray = res.message.reverse();
        console.log(this.postArray);
      }, (err) => console.error(err)
    );
  }

  translatePost(post: any){
    this.userService.translateValue(post.publicacion).subscribe(
      (res: any) => post.publicacion = res.message, 
      (err) => console.error(err)
    )
  }

}
